@import '~@fullcalendar/core/main.css';
@import '~@fullcalendar/daygrid/main.css';
@import '~@fullcalendar/timegrid/main.css';
@import '~@fullcalendar/list/main.css';

.app-calendar {
  margin: 0 auto;
  /* max-width: 1600px; */
}

.columnHeaderHtml {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.columnHeaderHtml p {
  margin: 0;
  color: #333;
  font-size: 14px;
}

.fc-content {
  font-size: 1.3em;
  color: #fff;
  font-weight: 500;
  padding: 5px;
}

.fc.fc-ltr.fc-unthemed {
  flex-grow: 1;
}

.patientInfo p {
  margin-bottom: 0;
}

.doctorCheckboxItem {
  display: flex;
  margin-bottom: 10px;
}

.doctorCheckboxItem label {
  font-size: 14px;
  font-weight: 600;
}

.fc-axis {
  width: 50px !important;
}

.fc-list-item-title.fc-widget-content {
  color: #000;
}

.fc-button.fc-button-primary {
  background-color: #fff !important;
  color: rgba(0, 0, 0, 0.65) !important;
  border-color: #d9d9d9 !important;
}

.fc-toolbar.fc-header-toolbar {
  background: #f3f8ff;
  padding: 17px 23px 17px 38px;
  margin-bottom: 0;
  border-left: 1px solid rgb(221, 221, 221);
  border-right: 1px solid rgb(221, 221, 221);
}

.popover .ant-row {
  margin-top: 8px;
  margin-bottom: 8px;
}
